#container {
  background-color: #bcbdbd;
  width: 100%; }

#content {
  background-color: white;
  margin: 30px; }

.subheadline {
  font-family: sans-serif;
  color: #007fbf;
  font-size: 15px;
  margin: 25px 0px 5px 0px; }

.text {
  font-family: sans-serif;
  color: #007fbf;
  font-size: 15px; }

.list {
  font-family: sans-serif;
  color: #007fbf;
  font-size: 15px; }

.nav a,
.nav a:visited,
.nav-mobile a,
.nav-mobile a:visited {
  font-family: sans-serif;
  text-decoration: none;
  color: white; }

.nav a:active,
.nav a:hover,
.nav-mobile a:active,
.nav-mobile a:hover {
  font-family: sans-serif;
  text-decoration: none;
  color: #00cd59; }

.list a,
.list a:visited,
.list a:active {
  font-family: sans-serif;
  color: #007fbf;
  text-decoration: none; }

.list a:hover {
  font-family: sans-serif;
  color: #00cd59;
  text-decoration: none; }
