@media screen and (max-width: 700px) {
  .nav,
  #image-header-desktop {
    display: none; }
  #image-header-mobile {
    background-color: #222222;
    grid-column-start: 1;
    grid-column-end: 6; }
  .nav-mobile {
    color: white;
    font-size: 20px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    clear: both; }
  #content {
    background-color: white;
    border-radius: 5px;
    margin: 5px;
    padding: 5px; }
  #request-info-button {
    font-size: 22px;
    color: #007fbf;
    cursor: pointer;
    width: 100%;
    height: 60px;
    background-color: white;
    border-radius: 5px;
    border-color: #007fbf;
    border-style: solid;
    border-width: 2px; }
  .headline {
    font-family: sans-serif;
    color: #007fbf;
    font-size: 22px;
    margin-bottom: 50px; } }
