.formTitle {
  color: #007fbf;
  font-size: 18px;
  text-align: left; }

.rfa_textinput {
  border-top: 0px;
  border-right: 0px;
  border-left: 0.5px solid #007fbf;
  border-bottom: 0.5px solid #007fbf;
  border-radius: 5px;
  padding: 8px; }

.rfa_textarea {
  border-top: 0px;
  border-right: 0px;
  border-left: 0.5px solid #007fbf;
  border-bottom: 0.5px solid #007fbf;
  border-radius: 5px;
  padding: 8px; }

.rfa_input-container {
  display: inline-block;
  margin: 14px;
  clear: both; }

.rfa_label,
.rfa_textarea-label {
  color: #007fbf;
  position: absolute;
  margin-top: -9px;
  margin-left: 10px;
  font-size: 14px; }

.rfa_input-error {
  color: red;
  font-size: 12px;
  margin-left: 50px;
  position: absolute; }

.rfa_textarea-error {
  color: red;
  font-size: 12px; }

.rfa_submit {
  margin: 0px;
  padding: 3px 18px 3px 18px;
  cursor: pointer;
  background-color: white;
  border-radius: 5px;
  border-color: #007fbf;
  border-style: solid;
  border-width: 2px; }

.buttondiv {
  clear: both;
  margin-left: 30px; }

.userNotify-success {
  color: green; }
